import LoanTypeService from '@/services/loan/LoanTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LoanTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultLoanTypeTableFilter: {
      type: String,
      default: null
    },
    defaultLoanTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      loanTypeTableData: [],
      loanTypeTableFilterTimer: null,
      loanTypeTableFilter: this.defaultLoanTypeTableFilter,
      loanTypeTableOrderBy: this.defaultLoanTypeTableOrderBy,
      loanTypeTableContainer: 'loading-container',
      loanTypeTableBusy: false,
      loanTypeTableTotalRows: 0,
      loanTypeTableCurrentPage: 1,
      loanTypeTablePerPage: 10
    }
  },
  computed: {
    loanTypeTableNoDataText: function () {
      return this.loanTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    loanTypeTableTotalPages: function () {
      return Math.ceil(this.loanTypeTableTotalRows / this.loanTypeTablePerPage)
    },
    loanTypeTablePagingInfo: function () {
      return this.pagingInfo(this.loanTypeTableCurrentPage, this.loanTypeTablePerPage, this.loanTypeTableTotalRows)
    }
  },
  methods: {
    loadLoanTypeTableData (loadingContainerId) {
      let self = this
      this.loanTypeTableBusy = true
      this.loanTypeTableContainer = loadingContainerId || this.loanTypeTableContainer
      this.startLoading(this.loanTypeTableContainer)
      LoanTypeService.loanTypes({
        pageIndex: this.loanTypeTableCurrentPage - 1,
        pageSize: this.loanTypeTablePerPage,
        filter: this.loanTypeTableFilter,
        orderByField: this.loanTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.loanTypeTableContainer)
        self.loanTypeTableBusy = false
        self.loanTypeTableTotalRows = response.data.totalRows
        self.loanTypeTableData = response.data.loanTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.loanTypeTableContainer)
        self.loanTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLoanTypeTable () {
      this.loadLoanTypeTableData()
    },
    filterLoanTypeTable (filter) {
      let self = this
      if (this.loanTypeTableFilter !== filter) {
        clearTimeout(this.loanTypeTableFilterTimer)
        this.loanTypeTableFilterTimer = setTimeout(() => {
          self.loanTypeTableFilter = filter
          self.loadLoanTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.loanTypeTableFilterTimer)
      }
    },
    orderLoanTypeTable (orderBy, active) {
      this.loanTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadLoanTypeTableData()
    }
  },
  watch: {
    loanTypeTableCurrentPage: function () {
      this.refreshLoanTypeTable()
    },
    loanTypeTablePerPage: function () {
      this.refreshLoanTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshLoanTypeTable()
    }
  }
}

export default LoanTypeTableMixin
